.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.sidebar{
  position: fixed;
  height: 100vh;
  width: 120px;
  background: #333;
  color: #fff;
  font-weight: 500;
}

.sidebar-menus{
  padding-left: 0;
  list-style: none;
  margin: 0px;
  padding-top: 50px;
}

.sidebar-menus li{
  padding: 15px 10px;
}

.main{
  margin-left: 120px;
  padding: 25px 15px;
}

.card{
  box-shadow: 0px 0px 5px 3px #0000001a;
  border-radius: 10px;
  padding: 20px 15px;
}

.table-main{
  width: 100%;
  text-align: left;
  padding: 20px 15px;
  border-collapse: collapse;
}

.table-header{
  height: 40px;
  background: #333;
  color: #fff;
}

.table-main th,
.table-main td{
  padding: 0px 10px;
}

.table-row {
  height: 55px;
  border-bottom: 1px solid lightgrey;
  transition: ease-in-out .3s;
}

.table-row:hover {
  background: #d3d3d34d;
}

.user-image {
  overflow: hidden;
  width: 46px;
  height: 46px;
  border-radius: 100%;
  border: 1px solid #333;
}

.user-image img{
  width: 100%;
}

.table-title{
  display: flex;
  align-items: center;
  height: inherit;
}

.table-title .name{
  padding-left: 15px;
  font-weight: bold;
}